.footer {
    max-width: 880px;
    width: 100%;
    padding: 0 36px;
}

@media screen and (max-width: 970px) {
    .footer {
        width: calc(100% - 72px);
    }
}

@media screen and (max-width: 567px) {
    .footer {
        padding: 0 19px;
        width: calc(100% - 38px);
    }
}