.element__like-btn {
    background-color: #fff;
    background-image: url(../../../images/element__like-btn.svg);
    width: 22px;
    height: 19px;
    transition: opacity .3s ease-in-out;
    cursor: pointer;
    border: none;
    padding: 0;
}

.element__like-btn:hover {
    opacity: .5;
}